<template>
    <div class="card" :style="{'background-image': 'url('+getImg+')'}" @click="$emit('click')">
    </div>
</template>

<script>
    export default {
        name: "PortfolioCard",
        computed:{
            getImg(){
                const img = require('../views/img/todos.png');
                return img;
            }
        }
    }
</script>

<style lang="scss" scoped>
    .card{
        padding: 16px;
        height: 400px;
        width: 400px;
        border-radius: 4px;
        margin: 8px;
        color: #FFFFFF;
        cursor: pointer;
        box-shadow: 0px 0px 20px 0px rgba(50, 50, 50, 0.3);
        background-repeat: no-repeat;
        background-size: cover;
        transition: 0.2s;
        opacity: 0.9;
    }
    .card:hover{
        transform: scale(0.95);
        opacity: 1;
        box-shadow: 0px 0px 20px 0px rgba(50, 50, 50, 0.5);
    }
</style>
